import message_item from "@/components/user/notify/message_item";

export default {
  name: "notify",
  components: {
    message_item
  },
  data() {
    return {
      page_num: undefined,
      page: 1,
      message_list: [],
      total_count: undefined,
      PAGE_LIMIT: 30
    }
  },
  created() {
    this.load_message_list(this.page);
  },
  methods: {
    update_pagination: function () {
      this.page_num = parseInt(this.total_count / this.PAGE_LIMIT) + 1;
    },
    on_page_change: function (e) {
      this.pgae = parseInt(e);
      this.load_message_list(this.page);
    },
    load_message_list: function (page) {
      this.message_list = [];
      var start = (parseInt(page) - 1) * this.PAGE_LIMIT;
      var limit = this.PAGE_LIMIT;
      this.$axios.get('/app/message/list', {
        params: {
          id: this.id,
          start: start,
          limit: limit
        }
      }).then((response) => {
        console.log("response:", response);
        this.message_list = response.data.messages;
        this.total_count = response.data.total_count;
        this.update_pagination();
        this.on_message_loaded();
      }).catch((error) => {
      });
    },
    on_message_loaded: function () {
      var user_message_ids = [];
      for (var i = 0; i < this.message_list.length; ++i) {
        if (this.message_list[i].status == 1) {
          user_message_ids.push(this.message_list[i]['_id']);
        }
      }
      if (user_message_ids) {
        this.$axios.post('/app/message/read', {
          user_message_ids: user_message_ids
        }).then((response) => {
          this.update_user_info();
        }).catch(function (error) {

        });
      }
    }
  }
}
